*,*:after,*:before {box-sizing:border-box;}
body {font-size:0.9rem;}
a {text-decoration:none;color:#333;}
#header {height:50px;position:relative;line-height:50px;}
#header h1 {position:absolute;left:0;}
#header h1 a img {width:50px;}
#menu {text-align: right;}
#menu a {display:inline-block;margin-right:10px;font-weight: bold;}
#menu button {border:none; background:none;vertical-align: middle;}
button img {width:60%;}

#main {width: 100%;}
#main img {width:100%;}

#slide {width:100%; position: relative;overflow: hidden;}
#slide img {width:100%;}
#slide ul {position:relative; left:0;width:300%;}
#slide ul li {width:33.33%;float:left;}

#roll {position:absolute;bottom:5px;left:50%;transform:translateX(-50%);}
#roll a {display: inline-block; width:10px;height:10px;border-radius: 5px;
         background: #fff; margin-right:5px;}
#roll a:nth-child(3) {margin-right:0;}
#roll .on {background:#000;}

#list {width:95%; margin:0 auto 3rem;position: relative;padding-bottom: 3rem;}
#list li {float: left; width:50%; padding-right:3px;}
#list h3 {text-align: center;margin:2rem 0;}
span {display: block; height:20px;}
.im {margin:10px 0 8px 0;}
.im img {height:18px;}
.txt {font-size: 11px;}
.txt1 {font-size:10px;}
#list  ul>li> a>img {width:100%;}

#roll2 {position:absolute;bottom:5px;left:50%;transform:translateX(-50%);}
#roll2 a {display:inline-block;width:10px;height:10px;border-radius:5px;
          background: tomato;  margin-right:5px;}
#roll2 a:nth-child(3) {margin-right:0;}
#roll2 .active {background:purple;}

#list1 {background: url(../public/img/back.jpg) no-repeat;background-size: cover;}
#list1 h3 {text-align: center;padding:2rem 0;}
.ban {text-align: center;}
.ban img {width:90%;}

#store {text-align:center;}
#store h3 {text-align: center;padding:2rem 0;}
#store img {width:95%;}

#deli {text-align:center;}
#deli h3 {text-align: center;padding:2rem 0;}
#deli img {width:95%;}

#sns {text-align:center;}
#sns h3 {text-align: center;padding:2rem 0;}
#sns ul {text-align: center;}
#sns ul li {display:inline-block;}
#sns ul li img {width:50px;margin-right:10px;}
#sns ul li:last-child img {margin-right:0;}

.line {position: relative; text-align: center;padding: 40px 16px 0 16px;
       margin-bottom: -14px;}
.line img {height:100%;}       
.line:before {position: absolute;top: 23px;left: 4.4%;display: inline-block;
              text-align: center; content: ""; width: 91.2%;   height: 1px;
              background: #dcd7d1; }
.line a {position: absolute;  top: 12px;left: 50%;  
         transform:translateX(-50%); 
         display: inline-block;  height: 8px; 
         text-align: center; z-index: 9; }    
         
#listimg {width:97%;margin:3rem auto;}
#listimg ul {display: flex;flex-wrap: wrap;}
#listimg ul li {width:50%;padding-right:3px;padding-bottom:2px;}
#listimg ul li img {width:100%;}      

#footer {border-top:1px solid #ffb02d; margin-top:20px;
    font-size: 0.75rem;padding:1rem;  }
#footer p {height:20px;}

